.main__label {
  width: 120px;
  height: 50px;
  background: beige;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  background: rgb(63, 171, 251);
  background: radial-gradient(
    circle,
    rgba(63, 171, 251, 1) 0%,
    rgba(70, 252, 250, 1) 100%
  );
}
